.to {
  font-size: 10px;
}

.courses-container .section-title {
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #aaa;
  margin: 0 0 5px;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1px;
}

.courses-container .section-title:after {
  content: "";
  background: #9ae1af;
  width: 120px;
  height: 1px;
  margin: 4px 10px;
  display: inline-block;
}

.courses-container .courses-loop {
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
}

.courses-container .courses-loop a {
  margin-block-start: 0;
}

.courses-container .courses-loop .course-item {
  border: 1px solid #eef0ef;
  border-radius: 5px;
  height: 100%;
  padding-bottom: 20px;
  text-decoration: none !important;
}

.courses-container .courses-loop .course-item .course-content {
  padding: 0 15px;
}

.courses-container .courses-loop .course-item img {
  width: auto;
  height: 250px;
}

.courses-container .courses-loop .course-item p {
  color: #777;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
}

.courses-container .courses-loop .course-item .custom-post-tags {
  display: flex;
}

.courses-container .courses-loop .course-item .custom-post-tags .tag-item {
  color: #fff;
  margin: 0;
  background: #be2c2c;
  margin-right: 15px;
  padding: 7px 14px;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
}

.faq-page {
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
}

.faq-page .title {
  margin: 2rem 0;
  font-size: 3rem;
}

.faq-page svg {
  width: 60px;
}

.faq-page .faq {
  cursor: pointer;
  background-color: #be2c2c;
  border-bottom: 2px solid #fff;
  width: 100%;
  margin-top: 2rem;
  padding: 10px 20px;
}

.faq-page .question {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.faq-page .question h3 {
  color: #fff;
  font-size: 1.8rem;
}

.faq-page .answer {
  color: #fff;
  max-height: 0;
  transition: max-height 1s ease-in-out;
  overflow: hidden;
}

.faq-page .answer p {
  padding-top: 1rem;
  font-size: 1.4rem;
  line-height: 1.6;
}

.faq-page .faq.active .answer {
  max-height: 300px;
}

.sigurd {
  color: #00f;
}

.test {
  color: red;
}

h1 {
  font-size: 48px;
  font-weight: 800;
}

h2 {
  font-size: 24px;
  font-weight: 700;
}
